body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

#banner{
    background-image: url(https://images.pexels.com/photos/34592/pexels-photo.jpg?auto=compress&cs=tinysrgb&dpr=2&h=650&w=940);
    background-position:center;
    background-size:cover;
    background-repeat:no-repeat;
    color:white;
    box-shadow: 5px 10px 8px #888888;
}

#banner h1 {
    margin:auto;
    text-align: center;
    opacity:0.95;
    border-radius:10px;
}
#searchForm {
    width:90%;
    min-height:25vh;
    height:auto;
    margin:auto;
    background-color:#343a40;
    color:white;
    border-radius:2px;
    box-shadow: 5px 10px 8px #888888;
}
#searchForm h1{
    width:100%;
    text-align:center;
}

#searchForm input{
    width:90%;
    margin-left:5%;
    text-shadow:none;
}

#searchForm #submitBtn {
    min-width:15vw;
    height:20%;
    margin:auto;
    margin-right:5%;
    margin-top:2%;
    float:right;
}
#results {
    width:90%;
    margin:auto;
    box-shadow: 5px 10px 8px #888888;
}
strong {
    color:white;
}
h3, a, p, h5, button{
    text-shadow:none;
    color:black;
}
.list-group-item p {
  font-size: 16px;
}

#mainHeader{
    width:100%;
    height:10vh;
        color:white;
}

